*, :after, :before {
  box-sizing: border-box;
}

:root {
  --color-text: #fff;
  --color-bg: #000;
  --color-link: #ffffff80;
  --color-bg-slide: #171719;
}

body {
  color: var(--color-text);
  background-color: var(--color-bg);
  text-transform: uppercase;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100vh;
  margin: 0;
  font-family: obviously, -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif;
  overflow: hidden;
}

.js .loading:before, .js .loading:after {
  content: "";
  z-index: 1000;
  position: fixed;
}

.js .loading:before {
  width: 100%;
  height: 100%;
  background: var(--color-bg);
  top: 0;
  left: 0;
}

.js .loading:after {
  width: 60px;
  height: 60px;
  opacity: .4;
  background: var(--color-link);
  border-radius: 50%;
  margin: -30px 0 0 -30px;
  animation: .7s linear infinite alternate forwards loaderAnim;
  top: 50%;
  left: 50%;
}

@keyframes loaderAnim {
  to {
    opacity: 1;
    transform: scale3d(.5, .5, 1);
  }
}

a {
  color: var(--color-link);
  outline: none;
  text-decoration: none;
}

p {
  margin: 0;
}

li {
  list-style-type: none;
}

a:hover {
  color: var(--color-link);
  outline: none;
}

.unbutton {
  font: inherit;
  cursor: pointer;
  color: var(--color-link);
  background: none;
  border: 0;
  margin: 0;
  padding: 0;
}

.unbutton:focus {
  outline: none;
}

section.blur {
  filter: blur(10px);
  transition: filter .25s ease-in-out;
}

.frame {
  z-index: 99;
  position: fixed;
}

.frame__nav {
  z-index: 2;
  background-color: #00000059;
  flex-direction: column;
  gap: .5rem;
  padding: 10px 0 16px;
  display: flex;
  position: absolute;
  top: 65vh;
  left: 10px;
}

.frame__nav-button {
  text-transform: uppercase;
  pointer-events: auto;
  white-space: nowrap;
  padding: 2px 20px;
  font-size: 13px;
  transition: color .3s;
  display: inline-block;
  position: relative;
}

.frame__nav-button.is-hidden {
  display: none;
}

.frame__nav-button:not(.frame__nav-button--current):hover {
  color: var(--color-link-hover);
}

.frame__nav-button--current {
  color: var(--color-text);
  scale: 1.1;
}

.frame__nav-button:before {
  content: "";
  width: 2rem;
  height: 1px;
  transform-origin: 100%;
  background-color: currentColor;
  transition: transform .8s ease-in-out;
  position: absolute;
  top: 50%;
  right: calc(100% - 15px);
  transform: scaleX(0);
}

.frame__nav-button--current:before {
  transform: scaleX(1);
}

.frame__info {
  fill: #fff;
  z-index: 2;
  opacity: .75;
  flex-direction: row;
  justify-self: center;
  align-items: center;
  margin-right: 2vw;
  animation: 4s infinite shake;
  display: flex;
  position: absolute;
  top: 50%;
  right: 0;
}

.slides {
  width: 100%;
  height: 100vh;
  grid-template-rows: 100%;
  grid-template-columns: 100%;
  place-items: center;
  display: grid;
  overflow: hidden;
}

.slide {
  width: 100%;
  height: 104%;
  pointer-events: none;
  opacity: 0;
  will-change: transform;
  grid-area: 1 / 1 / -1 / -1;
  position: relative;
  overflow: hidden;
}

.slide--current {
  pointer-events: auto;
  opacity: 1;
}

.slide__inner {
  width: 100%;
  height: 100%;
  will-change: transform;
  pointer-events: none;
  grid-template-rows: 100%;
  grid-template-columns: 100%;
  display: grid;
  position: relative;
  overflow: hidden;
}

.slide__img {
  cursor: pointer;
  z-index: 1;
  position: relative;
  overflow: hidden;
}

.slide__img-inner {
  width: 100%;
  height: 100%;
  will-change: transform;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }

  14% {
    transform: translateX(0);
  }

  16% {
    transform: translateX(-1px);
  }

  18% {
    transform: translateX(1px);
  }

  20% {
    transform: translateX(-3px);
  }

  22% {
    transform: translateX(3px);
  }

  24% {
    transform: translateX(-6px);
  }

  26% {
    transform: translateX(6px);
  }

  28% {
    transform: translateX(-9px);
  }

  30% {
    transform: translateX(9px);
  }

  32% {
    transform: translateX(-6px);
  }

  34% {
    transform: translateX(6px);
  }

  36% {
    transform: translateX(-3px);
  }

  38% {
    transform: translateX(3px);
  }

  40% {
    transform: translateX(-1px);
  }

  42% {
    transform: translateX(1px);
  }

  44% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(0);
  }
}

.splitting .word, .splitting .char {
  display: inline-block;
}

.splitting .char {
  position: relative;
}

.splitting .char:before, .splitting .char:after {
  content: attr(data-char);
  visibility: hidden;
  -webkit-user-select: none;
  user-select: none;
  transition: inherit;
  position: absolute;
  top: 0;
  left: 0;
}

.splitting {
  --word-center: calc((var(--word-total)  - 1) / 2);
  --char-center: calc((var(--char-total)  - 1) / 2);
  --line-center: calc((var(--line-total)  - 1) / 2);
}

.splitting .word {
  --word-percent: calc(var(--word-index) / var(--word-total));
  --line-percent: calc(var(--line-index) / var(--line-total));
}

.splitting .char {
  --char-percent: calc(var(--char-index) / var(--char-total));
  --char-offset: calc(var(--char-index)  - var(--char-center));
  --distance: calc((var(--char-offset) * var(--char-offset)) / var(--char-center));
  --distance-sine: calc(var(--char-offset) / var(--char-center));
  --distance-percent: calc((var(--distance) / var(--char-center)));
}

.splitting.cells img {
  width: 100%;
  display: block;
}

@supports (display: grid ) {
  .splitting.cells {
    visibility: hidden;
    background-size: cover;
    position: relative;
    overflow: hidden;
  }

  .splitting .cell-grid {
    background: inherit;
    width: 100%;
    height: 100%;
    grid-template: repeat(var(--row-total), 1fr) / repeat(var(--col-total), 1fr);
    display: grid;
    position: absolute;
    top: 0;
    left: 0;
  }

  .splitting .cell {
    background: inherit;
    position: relative;
    overflow: hidden;
  }

  .splitting .cell-inner {
    background: inherit;
    visibility: visible;
    width: calc(100% * var(--col-total));
    height: calc(100% * var(--row-total));
    left: calc(-100% * var(--col-index));
    top: calc(-100% * var(--row-index));
    position: absolute;
  }

  .splitting .cell {
    --center-x: calc((var(--col-total)  - 1) / 2);
    --center-y: calc((var(--row-total)  - 1) / 2);
    --offset-x: calc(var(--col-index)  - var(--center-x));
    --offset-y: calc(var(--row-index)  - var(--center-y));
    --distance-x: calc((var(--offset-x) * var(--offset-x)) / var(--center-x));
    --distance-y: calc((var(--offset-y) * var(--offset-y)) / var(--center-y));
  }
}

